import NavBar from '../../common/NavBar/NavBar';
import InputGroup from 'react-bootstrap/InputGroup';
import { Card, Col, Row } from 'antd';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Form from 'react-bootstrap/Form';
import './SearchPli.scss';
import { useNavigate } from 'react-router-dom';

const { Meta } = Card;

const SearchPli = () => {
  const navigate = useNavigate();
  const [plis, setPlis] = useState([]);
  const [value, setValue] = useState('');
  const [add, setAdd] = useState([]);
  const [spin, setSpin] = useState(true);
  const userId = !localStorage.getItem('userData')
    ? null
    : JSON.parse(localStorage.getItem('userData')).id;
  const fetchPliSearch = () => {
    if (localStorage.getItem('userData')) {
      axios.get('/api/pli/getplis/searchpli').then((res) => {
        if (res.data.success) {
          setPlis([...res.data.plis]);
          setTimeout(() => {
            setSpin(false);
          }, 300);
        } else {
          alert('  ');
        }
      });
    }
  };
  const search = (word) => {
    //str.replace(/(\s*)/g,'') str  .
    //      title, subtitle   result .
    let result = [];
    plis.map((pli, index) => {
      if (
        pli.title
          .replace(/(\s*)/g, '')
          .toUpperCase()
          .includes(word.replace(/(\s*)/g, '').toUpperCase())
      ) {
        result.push(pli);
      } else if (
        pli.subTitle
          .replace(/(\s*)/g, '')
          .toUpperCase()
          .includes(word.replace(/(\s*)/g, '').toUpperCase())
      ) {
        result.push(pli);
      }
    });
    setAdd([...result]);
  }; //        
  const onChange = (e) => {
    setValue(e.target.value);
  };

  useEffect(() => {
    fetchPliSearch();
  }, []);
  useEffect(() => {
    if (userId == null) {
      navigate('/login');
    }
    if (value.length >= 2) {
      //    
      search(value); //    
    } else {
      setAdd([]);
    }
  }, [value]);

  const renderCards = add.map((pli, index) => {
    return (
      <Col lg={4} md={6} xs={8} key={index}>
        <div style={{ position: 'relative' }}>
          <a href={`/plis/${pli._id}`}>
            <img
              style={{ width: '100%' }}
              alt='thumbnail'
              src={`${pli.thum}`}
            />
          </a>
        </div>
        <div style={{ paddingTop: '0.3rem', paddingBottom: '1rem' }}>
          <Meta title={pli.title} />
          <span>
            {pli.subTitle}, Likes:{pli.likes}{' '}
          </span>
        </div>
      </Col>
    );
  });

  return (
    <div>
      <div
        style={{ width: '85%', margin: '1.5rem auto', paddingBottom: '3rem' }}
      >
        <InputGroup className='mb-3'>
          <Form.Control
            placeholder=' '
            onChange={onChange}
            value={value}
          />
        </InputGroup>
        <hr />

        <Row gutter={16}>{renderCards}</Row>
        {!(value.length >= 2) && (
          <div
            className='text-center'
            style={{
              justifyContent: 'center',
            }}
          >
            <p> </p>
            <p> </p>
            <p>   </p>
            <p>   </p>
          </div>
        )}
      </div>
      <NavBar value={1} />
      {spin && (
        <svg className='spinner' viewBox='0 0 50 50'>
          <circle
            className='path'
            cx='25'
            cy='25'
            r='20'
            fill='none'
            strokeWidth='5'
          ></circle>
        </svg>
      )}
    </div>
  );
};

export default SearchPli;
