import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Card, Col, Typography, Row } from 'antd';
import NavDropdown from 'react-bootstrap/NavDropdown';
import NavBar from '../../common/NavBar/NavBar';
import Button from '@mui/material/Button';
import LoginPage from '../LoginPage/LoginPage';
import './LandingPage.scss';
import { useNavigate } from 'react-router-dom';
const { Title } = Typography;
const { Meta } = Card;
const LandingPage = () => {
  const navigate = useNavigate();
  const [plis, setPlis] = useState([]);
  const [Align, setAlign] = useState('Latest Updated');
  const [page, setPage] = useState(1);
  const [spin, setSpin] = useState(false);
  const userId = !localStorage.getItem('userData')
    ? null
    : JSON.parse(localStorage.getItem('userData')).id;

  //       .
  //   useEffect    1 
  const setLatest = () => {
    setPlis([]);
    setPage(1);
    setAlign('Latest Updated');
  };
  const setOldest = () => {
    setPlis([]);
    setPage(1);
    setAlign('Oldest Updated');
  };
  const setFavorite = () => {
    setPlis([]);
    setPage(1);
    setAlign('Number of favorite');
  };
  const spining = () => {
    setSpin(true);
    setTimeout(() => {
      setSpin(false);
    }, 800);
  };
  const loadMoreItems = () => {
    setSpin(true);
    if (Align === 'Latest Updated') {
      fetchPlisLatest();
      setPage(page + 1);
    } else if (Align === 'Oldest Updated') {
      fetchPlisOldest();
      setPage(page + 1);
    } else if (Align === 'Number of favorite') {
      fetchPlisFavorite();
      setPage(page + 1);
    }
    setSpin(false);
  };
  const checkAuth = (callbackFunc) => {
    axios.post('/api/users/auth', { id: userId }).then((res) => {
      if (res.data.auth == 0) {
        logout();
      }
      if (res.data.auth == 1) {
        callbackFunc();
        spining();
      }
    });
  };
  const logout = () => {
    localStorage.clear();
    navigate('/login');
  };

  // [...plis, ...res]      .
  //      
  const fetchPlisLatest = () => {
    axios
      .post('/api/pli/getPlis/latest', {
        pageNum: page,
      })
      .then((res) => {
        if (res.data.success) {
          setPlis([...plis, ...res.data.plis]);
          // 2  
        } else {
          alert('  ');
        }
      });
  };
  const fetchPlisFavorite = () => {
    axios
      .post('/api/pli/getPlis/favorite', {
        pageNum: page,
      })
      .then((res) => {
        if (res.data.success) {
          setPlis([...plis, ...res.data.plis]);
        } else {
          alert('  ');
        }
      });
  };
  const fetchPlisOldest = () => {
    axios
      .post('https://share-vibe-pli.herokuapp.com/api/pli/getPlis/oldest', {
        pageNum: page,
      })
      .then((res) => {
        if (res.data.success) {
          setPlis([...plis, ...res.data.plis]);
        } else {
          alert('  ');
        }
      });
  };
  useEffect(() => {
    if (userId == null) {
      navigate('/login');
    }
    setSpin(true);
    checkAuth(loadMoreItems);
    //    

    //     
  }, [Align]); // Align value    loaditems ,    
  //    .

  //   thum   ,  .thum   div
  //     .
  const renderCards = plis.map((pli, index) => {
    return (
      <Col lg={6} md={8} xs={12} key={index}>
        <div style={{ position: 'relative' }}>
          <a href={`/plis/${pli._id}`}>
            <img
              style={{ width: '100%' }}
              alt='thumbnail'
              src={`${pli.thum}`}
            />
          </a>
        </div>
        <div style={{ paddingTop: '0.3rem', paddingBottom: '1rem' }}>
          <Meta title={pli.title} />
          <span>
            {pli.subTitle}, Likes:{pli.likes}
          </span>
        </div>
      </Col>
    );
  });

  return (
    <div>
      <div style={{ width: '85%', margin: '1.5rem auto' }}>
        <Title level={3} style={{ display: 'inline-block' }}>
          {Align}
        </Title>
        <NavDropdown
          style={{
            display: 'inline-block',
            float: 'right',
            paddingLeft: '1rem',
          }}
          id='nav-dropdown-dark-example'
          title=''
          menuVariant='dark'
        >
          {/*  */}
          {Align !== 'Latest Updated' && (
            <NavDropdown.Item onClick={setLatest}>
                
            </NavDropdown.Item>
          )}
          {Align !== 'Number of favorite' && (
            <NavDropdown.Item onClick={setFavorite}> </NavDropdown.Item>
          )}
          {Align !== 'Oldest Updated' && (
            <NavDropdown.Item onClick={setOldest}> </NavDropdown.Item>
          )}
        </NavDropdown>
        <hr />
      </div>

      {spin && (
        <svg className='spinner' viewBox='0 0 50 50'>
          <circle
            className='path'
            cx='25'
            cy='25'
            r='20'
            fill='none'
            strokeWidth='5'
          ></circle>
        </svg>
      )}
      <div style={{ width: '85%', margin: '1.5rem auto' }}>
        <Row gutter={16}>{renderCards}</Row>
      </div>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          paddingBottom: '5rem',
        }}
      >
        <Button variant='outlined' size='small' onClick={loadMoreItems}>
          Load more
        </Button>
      </div>
      <NavBar value={0} />
    </div>
  );
};

export default LandingPage;
