import { Button, ButtonGroup } from '@mui/material';
import PersonAddIcon from '@mui/icons-material/PersonAdd';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import FavoriteIcon from '@mui/icons-material/Favorite';
import { red } from '@mui/material/colors';
import axios from 'axios';
import { useEffect, useState } from 'react';
import FollowPeople from './FollowPeople';

const Follow = (props) => {
  const [followNum, setFollowNum] = useState(0);
  const [followList, setFollowList] = useState([]);
  const [followed, setFollowed] = useState(false);
  const [open, setOpen] = useState(false);
  let body = {
    userTo: props._id, //    _id
    userFrom: props.userId, // id
  };

  const checkFollowed = () => {
    axios.post('/api/follow/Followed', body).then((res) => {
      if (res.data.success) {
        setFollowed(res.data.Followed);
      } else {
        //   
      }
    });
  };

  const getFollowList = () => {
    axios.post('/api/follow/followpeople', body).then((res) => {
      if (res.data.success) {
        setFollowList(res.data.list);
        setFollowNum(res.data.list.length);
      } else {
        //   
      }
    });
  };

  const openList = () => {
    if (followNum !== 0) {
      axios.post('/api/follow/followpeople', body).then((res) => {
        if (res.data.success) {
          setFollowList(res.data.list);
          setOpen(true);
        } else {
          //   
        }
      });
    }
  };

  const addFollow = () => {
    if (!followed) {
      axios.post('/api/follow/addToFollow', body).then((res) => {
        if (res.data.followSuccess) {
          setFollowed(!followed);
          setFollowNum(followNum + 1);
        }
      });
    } else {
      axios.post('/api/follow/removeFromFollow', body).then((res) => {
        if (res.data.removeSuccess) {
          setFollowed(!followed);
          setFollowNum(followNum - 1);
        }
      });
    }
  };

  useEffect(() => {
    checkFollowed();
    getFollowList();
  }, []);

  return (
    <div>
      <ButtonGroup variant='outlined' aria-label='outlined button group'>
        <Button
          onClick={addFollow}
          style={{ fontSize: '0.75rem' }}
          startIcon={
            followed ? (
              <FavoriteIcon
                style={{ marginRight: '-0.2rem' }}
                sx={{ color: red[500] }}
              />
            ) : (
              <FavoriteBorderIcon
                style={{ marginRight: '-0.2rem' }}
                sx={{ color: red[500] }}
              />
            )
          }
        >
          {followNum} {followed ? 'followed' : 'follow'}
        </Button>
        <Button onClick={openList}>list</Button>
      </ButtonGroup>
      <FollowPeople open={open} list={followList} onClose={setOpen} />
    </div>
  );
};

export default Follow;
